@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-mi {
    padding: 5rem 0;

    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}
.portafolio{
    color: $negro;
    text-decoration: underline ;
}
.contenido-demi {
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include m.tablet {
        margin-top: 0;
    }
    h2 {
        margin-bottom: 2rem;
    }
    .lugar {
        color: v.$negro;
        font-weight: 700;
        text-align: center;
    }
}