@include desktop{
    .logoes {
        display: flex;
        font-family: monospace;
        white-space: nowrap;
        border-right: 4px solid;
        width: 18ch;
    
        animation: typing 2s steps(9),
        blink .5s infinite step-end alternate;
        overflow: hidden;
    }
    
    @keyframes typing {
        from { width: 0;}
    }
    
    @keyframes blink {
        50% {border-color: transparent;}
    }
}
