@use 'base/variables' as v;
@use 'base/mixins' as m;

@include chico {
    .iconos{
        display: grid;
        gap: 5rem;
        grid-template-columns: repeat(2, 1fr);
        margin-bottom: 5rem;
    }
}

@include telefono {
    .iconos{
        display: grid;
        gap: 5rem;
        grid-template-columns: repeat(3, 1fr);
        margin-bottom: 5rem;
    }
}

@include tablet {
    .iconos{
        display: grid;
        gap: 5rem;
        grid-template-columns: repeat(3, 1fr);
        margin-bottom: 5rem;
    }
}
@include desktop {
    .iconos{
        display: grid;
        gap: 5rem;
        grid-template-columns: repeat(6, 1fr);
        margin-bottom: 5rem;
    }
}
.icono{
    &:hover{
        transition: transform 500ms;
        transform: scale(1.1);
    }
}