.formulario {
    background-color: #ccc;
    margin: 1rem auto 1rem auto;
    width: 95%;
    padding: 5rem;
}
.campo {
    display: flex;
    margin-bottom: 2rem;
}
.campo__label {
    flex: 0 0 9rem;
    text-align: right;
    padding-right: 2rem;
}
.campo__field {
    flex: 1;
    border: 1px solid var(--gris);
}
.campo__field--textarea {
    height: 20rem;
}
.campo {
    display: flex;
    justify-content: flex-end;
}
.boton {
    background-color: black;
    text-transform: uppercase;
    padding: 1rem;
    color: $blanco;
    font-weight: 700;
    border: none;
    &:hover{
        cursor: pointer;
        background-color: rgba($color: #000000, $alpha: .6);
        font-weight: 400;
        transition: transform 300ms;
        transform: scale(1.05);
    }
}

