.check {
  margin-top: 1.1rem;
  position: relative;
  width: 5rem;
}

.check:before {
  content: "";
  position: absolute;
  width: 5rem;
  height: 2.5rem;
  background: #333;
  border-radius: 2.5rem;
}

.check:after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  background: #fff;
  border-radius: 2.5rem;
  transition: 0.5s;
  border: 2px solid #333;
  box-sizing: border-box;
}

.check:checked:after {
  left: 2.5rem;
  border: 2px solid #00a1ff;
}

.check:checked:before {
  background: #00a1ff;
}

