@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 5rem 0;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes1 {
    @include resetear-lista;
    @include grid(1, 2rem);
    @include telefono {
        @include grid(1, 2rem);
    }
    @include tablet {
        @include grid(2, 2rem);
    }
    @include desktop {
        @include grid(5, 2rem);
    }
}

