.galeria-imagenes1:hover {
        cursor: pointer;
    }
    .galeria-imagenes2:hover {
        cursor: pointer;
    }
    .galeria-imagenes3:hover {
        cursor: pointer;
    }
    .galeria-imagenes4:hover {
        cursor: pointer;
    }
    .galeria-imagenes5:hover {
        cursor: pointer;
    }

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: auto;
    height: auto;

    display: grid;
    place-items: center;

    background-color: rgba( 0, 0, 0, .5);

    picture {
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }
}
