footer a{
    text-decoration: none;
    color: white;
    transition: all 300ms;
}

.footer {
    background-color: $negro;
    font-family: $fuente_principal;
    font-size: 2.4rem;
    margin-top: 3rem;
    h1 {
        text-align: center; 
        @include tablet {
            margin: 0;
        }
    }
    .contenido-footer {
        padding-bottom: 1rem;
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    @include chico{
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    @include tablet {
        .contenido-footer{
            display: flex;
            align-items: center;
        }
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }   
}
.logo__nombre {
    font-weight: 400;
}
.logo__bold {
    font-weight: 750;
}
.contenido-header {
    justify-content: space-between;
}
a, .copy{
    color: $blanco;
    text-align: center;
}
.navegacion {
    @include tablet {
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        text-align: center;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        &:hover{
            transform: scale(1.1);
            }
        @include tablet {
            margin:0;
        }}
}