header a{
    text-decoration: none;
    color: white;
    transition: all 300ms;
}

.header {
    background-color: $negro;
    font-family: $fuente_principal;
    font-size: 2.4rem;
    h1 {
        text-align: center; 
        @include tablet {
            margin: 0;
        }
    }
    @include chico{
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    @include tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }   
}
.logo__nombre {
    font-weight: 400;
}
.logo__bold {
    font-weight: 750;
}
a {
    color: $blanco;
}
@include desktop{
    .logo {
        display: flex;
        font-family: monospace;
        white-space: nowrap;
        border-right: 4px solid;
        width: 16ch;
    
        animation: typing 2s steps(9),
        blink .5s infinite step-end alternate;
        overflow: hidden;
    }
    
    @keyframes typing {
        from { width: 0;}
    }
    
    @keyframes blink {
        50% {border-color: transparent;}
    }
}
@include tablet{
    .logo {
        display: flex;
        font-family: monospace;
        white-space: nowrap;
        border-right: 4px solid;
        width: 16ch;
    
        animation: typing 2s steps(9),
        blink .5s infinite step-end alternate;
        overflow: hidden;
    }
    
    @keyframes typing {
        from { width: 0;}
    }
    
    @keyframes blink {
        50% {border-color: transparent;}
    }
}
@include desktopXXL{
    .logo {
        display: flex;
        font-family: monospace;
        white-space: nowrap;
        border-right: 4px solid;
        width: 12ch;
    
        animation: typing 2s steps(9),
        blink .5s infinite step-end alternate;
        overflow: hidden;
    }
    
    @keyframes typing {
        from { width: 0;}
    }
    
    @keyframes blink {
        50% {border-color: transparent;}
    }
}

.mobile-menu{
    @include tablet {
        display: none;
    }
    img{
        width: 5rem;
    }
}
@include chico{
    .barra{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .navegacion{
        display: none;
    }
}
.mostrar {
    display: flex;
    flex-direction: column;
    @include tablet {
        flex-direction: row;
    }
}
.barra {
    @include tablet {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

